
// TYPES
import types from '../actions/types';

// initial values
const initialValue = {
    authInfo: null
}

export default function authReducer(state = initialValue, action) {
    switch (action.type) {

            
        case types.AUTH_INFO:
            return { 
                ...state,
                authInfo: action.payload.authInfo
            };

        default:
            return state;
    }
}
